.poke {
    margin: -173px 15px -173px -20px;
    width: 200px;
}
@keyframes pokeball {
	0% { -webkit-transform: translate(1px, -1px) rotate(0deg); }
	10% { -webkit-transform: translate(-1px, -2px) rotate(40deg); }
	20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); }
	30% { -webkit-transform: translate(0px, 2px) rotate(-30deg); }
	40% { -webkit-transform: translate(1px, -1px) rotate(1deg); }
	50% { -webkit-transform: translate(-1px, 2px) rotate(40deg); }
	60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); }
	70% { -webkit-transform: translate(2px, 1px) rotate(-30deg); }
	80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); }
	90% { -webkit-transform: translate(2px, 2px) rotate(40deg); }
	100% { -webkit-transform: translate(1px, -1px) rotate(-1deg); }
}
.poke:hover{ 
	-webkit-animation-name: pokeball;
	-webkit-animation-duration:1.5s;
	-webkit-transform-origin:50% 50%;
	-webkit-animation-iteration-count: infinite;
	-webkit-animation-timing-function: linear;
}

.logo{
    width: 200px;
    margin-left: 75px;
}
.btn{
    background-color: white;
    color: red;
    border-color: red;
}
.btn:hover  {
    background-color: red;
    color: white;
    border-color: white;
}